.flash-news-items-wrapper {
  position: relative;
}

.flash-news-items-wrapper .item {
  position: absolute;
  opacity: 0;
  transform: translate(0, 200%);
}

.flash-news-items-wrapper .item.prev {
  transform: translate(0, -200%);
  opacity: 0;
  transition: transform 500ms ease-in, opacity 300ms ease-in;
}

.flash-news-items-wrapper .item.active {
  opacity: 1;
  transform: translate(0, 0);
  transition: transform 500ms ease-in;
}
